import React from 'react'
import './exp.css';
import FooterExp from './footer/footer';

const ColoredLine = ({ color }) => (
    <hr
        style={{
            width: '90%',
            color: color,
            backgroundColor: color,
            height: 5
        }}
    />
);

function Exp() {
    return (
        <div className='exp-container'>
            <div className='exp-title'>
                <h1>Skillss</h1>
            </div>

            <div className='exp-main-container'>
                <div className='exp-content-container'>
                    <div className='exp-content'>
                        <h4>C</h4>
                    </div>
                    <div className='exp-content'>
                        <h3>C++</h3>
                    </div>
                    <div className='exp-content'>
                        <h3>Java</h3>
                    </div>
                    <div className='exp-content'>
                        <h3>Flutter</h3>
                    </div>
                </div>
                <div className='exp-content-container'>
                    <div className='exp-content'>
                        <h3>React.js</h3>
                    </div>
                    <div className='exp-content'>
                        <h4>HTML</h4>
                    </div>
                    <div className='exp-content'>
                        <h3>CSS</h3>
                    </div>
                    <div className='exp-content'>
                        <h3>Python</h3>
                    </div>
                </div>
                <FooterExp />
            </div>

        </div>
    )
}

export default Exp