import React from 'react'
import './edu.css';

const ColoredLine = ({ color }) => (
    <hr
        style={{
            width: '100%',
            color: color,
            backgroundColor: color,
            height: 1
        }}
    />
);

function Edu() {
    return (
        <div className='edu-container'>
            <div className='edu-title'>
                <h1>My Projects</h1>
            </div>

            <div className='edu-main-container'>
                <h4>TukangTravel Mobile Apps</h4>
                <ColoredLine color={'white'} />
                <a href='https://drive.google.com/file/d/1mdOgJiRhcAkxwNqIgF2ZtGcJu0_ScgBA/view?usp=sharing' target="_blank">Klik disini untuk mencoba pada platform android</a>
            </div>
        </div>
    )
}

export default Edu