import React from 'react'
import './aboutme.css';
import pasfoto from '../../assets/aboutme/pasfoto.jpg'

export default function AboutMe() {
  return (
    <div className='aboutme-container'>
      <div className='aboutme-title'>
          <h1>About me</h1>
      </div>
      <div className='aboutme-rectangle'>
        <div className='aboutme-photo'>
          <img src={pasfoto} width="100%" height="100%" />
        </div>
        <span className='aboutme-description'>
          Hello I'm Steven and I am a computer science undergraduate at BINUS University. As an undergraduate, I'm currently focusing on improving my skill especially in programming and problem-solving to later support my career path.
          <br /><br />
          I also try to improve my organization skill with being in CSC BINUS as an coordinator of education division.
        </span>
      </div>
    </div>
  )
}
